.products {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 70px;

  &__card {
    border-radius: 5px;
    transition: transform 0.4s;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__name {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
  }

  &__image {
    width: 400px;
    height: auto;
    border-radius: 4px;
  }
}
